/* Hamburger Styles */
$height-icon: 15px;
$width-line: 28px;
$height-line: 2px;

$transition-time: 0.4s;
$rotation: 45deg;
$translateY: $height-icon / 2;
$translateX: 0;

.hamburger-wrapper {
    right: 20px;
    display: flex;
    padding-top: 5px;
    padding-bottom: 10px;

}

.hamburger-icon {
    width: $width-line;
    height: $height-icon;
    margin: auto;
    position: relative;
    display: block;

    .line {
        display: block;
        background: $grey;
        width: $width-line;
        height: $height-line;
        position: absolute;
        left: 0;
        border-radius: $height-line / 2;
        transition: all $transition-time;
        -webkit-transition: all $transition-time;
        -moz-transition: all $transition-time;

        &.line-1 {
            top: 0;
        }

        &.line-2 {
            top: 50%;
        }

        &.line-3 {
            top: 100%;
        }
    }
}

