 /* Modal */
$speed: 0.6s;
$delay: $speed * 0.5;
$easing: cubic-bezier(0.55, 0, 0.1, 1);

/* Overlay
only show for tablet and up */

@media only screen and (min-width: 40em) {
    .modal-overlay {
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 5;
        background-color: #fff;
        opacity: 0;
        visibility: hidden;
        backface-visibility: hidden;

        &.active {
            opacity: 1;
            visibility: visible;
        }
    }
}

/* Modal */

.modal {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin: 0 auto;
    background-color: #fff;
    width: 920px;
    max-width: 75rem;
    min-height: 20rem;
    padding: 1rem;
    border-radius: 3px;
    opacity: 0;
    overflow-y: auto;
    visibility: hidden;
    backface-visibility: hidden;
    transform: scale(1.2);
    transition: all $speed $easing;

    .close-modal {
        position: absolute;
        cursor: pointer;
        top: 5px;
        right: 15px;
        opacity: 0;
        backface-visibility: hidden;
        transition: opacity $speed $easing, transform $speed $easing;
        transition-delay: $delay;

        svg {
            width: 1.75em;
            height: 1.75em;
        }
    }

    /* close modal */

    .modal-content {
        width: 100%;
        opacity: 0;
        backface-visibility: hidden;
        transition: opacity $speed $easing;
        transition-delay: $delay;
        padding: 20px;
    }

    /* content */

    &.active {
        visibility: visible;
        opacity: 1;
        transform: scale(1);

        .modal-content {
            opacity: 1;
        }

        .close-modal {
            transform: translateY(10px);
            opacity: 1;
        }
    }

    &.navigation {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        -webkit-overflow-scrolling: touch;
        border-radius: 0;
        transform: scale(1.1);
        padding: 0 !important;
        z-index: 1000;

        .modal-content {
            padding: 0;
        }

        .main-navigation {
            background: none;
            -webkit-box-shadow: none;
            -moz-box-shadow: none;
            box-shadow: none;
        }

        .close-modal {
            padding: 20px;
            right: 20px !important;
            top: 20px !important;
        }
    }
}

